@charset "UTF-8";
.router-view {
  position: relative; }

/* 可以设置不同的进入和离开动画 */
/* 设置持续时间和动画函数 */
.pop-enter-active, .push-enter-active {
  transition: all .3s ease; }

.pop-enter {
  transform: translateX(-100%);
  opacity: 0; }

.push-enter {
  transform: translateX(100%);
  opacity: 0; }



