










































































.loading-bar{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 1px;
    z-index: 100000;
}
.loading-bar_bar{
    position: absolute;
    top: 0;
    left: 0;
    height: 1px;
    width: 0;
    background-color: #66CCFF;
    transition: width .2s;
}



